<template>
    <div class="results-container">
        <b-row>
            <b-col md="12">
                <b-nav-form class="mb-4 search-bar">
              <b-form-input size="sm" class="mr-sm-2" placeholder="Search" v-model="name" :state="validName"></b-form-input>
              <button class="my-2 my-sm-0 btn-brand" type="submit" @click="goSearchResults">Search</button>
        </b-nav-form> 
                <div v-if="rests">
                    <h2>All Results</h2>
                    <div v-for="each in rests" :key="each.id">
                    <SingleRes :rest="each"></SingleRes>
                    </div>
                </div>
                <div v-else>
                    <div class="text-center text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                        <strong>Loading...</strong>
                    </div>
                </div>
            </b-col>
        </b-row> 
    </div>
  </template>
  
  <script>
import { ref, watchEffect } from 'vue'
import SingleRes from '../components/SingleRes.vue'
import { useRouter } from 'vue-router'

    export default {
        components: { SingleRes },
        props: ['name'],
        setup(props) {
            const isBusy = ref(false)

            const rests = [
                {name: 'Burger King', category: 'Fastfood', stars: 4.5, numberReviews: 1, id: 1},
                {name: 'Ad King', category: 'Fastfood', stars: 4.5, numberReviews: 2, id: 2}
            ]

            const router = useRouter()


            const validName = ref(null)
            const goSearchResults = () => {
            validName.value = null
            if (props.name && props.name) {
                router.push({ name: 'searchResults', params: { name: props.name}})
            } else {
                validName.value = false
            }
            }

            watchEffect(() => {
            if (props.name && props.name.length) {
                validName.value = null
            }
            })

            return {
                isBusy,
                rests,
                goSearchResults,
                validName
            }
        },
    }
  </script>

  <style scoped>

    .results-container {
        padding: 40px;
        padding-top: 0;
        font-family: 'Josefin Sans', sans-serif;
        margin-bottom: 100px;
    }

    .search-bar {
        width: 100%;
        margin: 20px 0;
    }


  </style>