<template>
  <div class="rest-container">
    <div class="image-container">
        <div class="rest-image"></div>
    </div>
    <div class="rest-info">
        <h1>{{ rest.name }}</h1>
        <div class="ratings">
            <div class="empty-stars"></div>
            <div class="full-stars" :style=" { width: starPercentage }"></div>
        </div>
        <span class="reviews-num">{{ rest.numberReviews }} reviews</span>
        <div class="tag mt-2">{{rest.category}}</div>
        <div class="mt-3 mb-3">3070 Granville Street Vancouver, BC V6H 3J8 Canada</div>
        <button @click="toWriteReview" class="btn-brand">Write a review</button>
        <button @click="showReservationModal = !showReservationModal" class="btn-brand">Make a rerservation</button>
        <b-modal centered v-model="showReservationModal"
                    @ok="handleReservation" @cancel="resetModal()" @hidden="resetModal()" hide-footer hide-header>
                    <template #default>
                        <h2 class="modal-title">Make a Reservation</h2>
                        <Datepicker v-model="date" required :minDate="new Date()" :maxDate="maxDate"
                        preventMinMaxNavigation :enableTimePicker="false"></Datepicker>
                        <b-row>
                          <b-col sm="6">
                            <b-form-select v-model="time" class="form-control">
                                <b-form-select-option value="5:00pm">5:00 pm</b-form-select-option>
                                <b-form-select-option value="5:30pm">5:30 pm</b-form-select-option>
                                <b-form-select-option value="6:00pm">6:00 pm</b-form-select-option>
                                <b-form-select-option value="6:30pm">6:30 pm</b-form-select-option>
                                <b-form-select-option value="7:00pm">7:00 pm</b-form-select-option>
                                <b-form-select-option value="7:30pm">7:30 pm</b-form-select-option>
                            </b-form-select>
                          </b-col>

                          <b-col sm="6">
                            <b-form-select v-model="numPeople" class="form-control">
                          <b-form-select-option class="select-option" value="1">1 People</b-form-select-option>
                          <b-form-select-option value="2">2 People</b-form-select-option>
                          <b-form-select-option value="3">3 People</b-form-select-option>
                          <b-form-select-option value="4">4 People</b-form-select-option>
                          <b-form-select-option value="5">5 People</b-form-select-option>
                          <b-form-select-option value="6">6 People</b-form-select-option>
                          <b-form-select-option value="7">7 People</b-form-select-option>
                      </b-form-select>
                          </b-col>
                        </b-row>
                          <button class="btn-brand full-width" @click="findTable">Find a table</button>
                          <div v-if="foundTables" class="time-buttons">
                            <button class="btn-brand time-button">5:00 pm</button>
                            <button class="btn-brand time-button">5:30 pm</button>
                            <button class="btn-brand time-button">6:00 pm</button>
                            <button class="btn-brand time-button">7:00 pm</button>
                          </div>
                    </template>
        </b-modal>
    </div>
    <div>
        
    </div>
    <div class="reviews" v-if="reviews">
        <div v-for="review in reviews" :key="review.id">
            <SingleReview :review="review"/>
        </div>
    </div>
  </div>
</template>

<script>
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { addMonths, getMonth, getYear, subMonths } from 'date-fns';

import { computed, watchEffect } from '@vue/runtime-core'
import SingleReview from '../components/SingleReview.vue'
import getStar from '../Composables/StarCalculator'
import { useRouter } from 'vue-router'
import { ref } from 'vue'

export default {
    components: { SingleReview, Datepicker },
    setup() {
        const rest = {name: 'Burger King', category: 'Fastfood', stars: 8, numberReviews: 2, id: 1}
        const reviews = [
            {id: 1, user: {name: 'Megan', id: '1'}, review: 'lalallalala'},
            {id: 1, user: {name: 'Megan', id: '2'}, review: 'lalallalala'}
        ]

        const starPercentage = computed(() => {
            return getStar(rest.stars, rest.numberReviews)
        })

        const router = useRouter()
        const toWriteReview = () => {
            router.push({ name: 'write', params: { id: rest.id }})
        }

        // Reservations
        const showReservationModal = ref(false)
        const resetModal = () => {
          showReservationModal.value = false
        }

        const maxDate = computed(() => addMonths(new Date(getYear(new Date()), getMonth(new Date())), 2));

        const date = ref(null)
        const numPeople = ref(2)
        const time = ref('5:00pm')

        const foundTables = ref(null)

        const findTable = () => {
          setTimeout(() => {
            foundTables.value = 2
          }, 1000)
        }



        return {
            rest,
            reviews,
            starPercentage,
            toWriteReview,
            showReservationModal,
            date,
            maxDate,
            numPeople,
            resetModal,
            foundTables,
            findTable,
            time
        }
    }
}
</script>

