<template>
  <div class="review-container">
    <b-row class="user-info" v-if="!byUser">
            <img src="../assets/avatar1.png" alt="">
            <div class="text">
                <p>Megan Ma</p>
            <p>Vancouver, Canada</p>
            </div>
    </b-row>
    <b-row class="user-info" v-else>
            <img src="../assets/fastfood.jpeg" alt="">
            <div class="text">
                <p class="rest-name">Burger King</p>
                <p>Vancouver, Canada</p>
            </div>
    </b-row>
    <div class="review-info">
        <div class="ratings">
            <div class="empty-stars"></div>
            <div class="full-stars" :style=" { width: starPercentage }"></div>
        </div>
        <span class="reviews-num">09/15/2022</span>
        <p>Neighborhood coffee haus with indoor dining and a couple of outdoor tables. Good selection of baked goods as noted on their website menu. Portions were on the small side but tasty. Service was relatively fast but quite a gap between ordering and getting drinks from the coffee bar. Bathrooms are poorly located, too close to dining area. Always preferable when customers have access to sugar, cream, knives and forks. All of that is done back of house so if you don't ask when ordering you have to line up again.</p>
    </div>
    <div class="review-btns" v-if="!byUser">
        <button @click="btnSelected('useful')" class="btn-brand" :class="{ 'selected': usefulSelected }"><font-awesome-icon icon="fa-regular fa-face-grin-stars" /> Useful {{usefulCount}}</button>
        <button @click="btnSelected('funny')" class="btn-brand" :class="{ 'selected': funnySelected }"><font-awesome-icon icon="fa-regular fa-face-smile" /> Funny {{funnyCount}}</button>
        <button @click="btnSelected('cool')" class="btn-brand" :class="{ 'selected': coolSelected }"><font-awesome-icon icon="fa-regular fa-face-surprise" /> Cool {{coolCount}}</button>
    </div>
    <div v-else class="review-btns">
        <button class="btn-brand">Delete review</button>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';

export default {
    props: [ 'review', 'byUser', 'ownerProfile'],
    setup() {
        const usefulSelected = ref(false)
        const funnySelected = ref(false)
        const coolSelected = ref(false)

        const dbUsefulCount = 0
        const dbFunnyCount = 0
        const dbcoolCount = 0

        const usefulCount = ref(0)
        const funnyCount = ref(0)
        const coolCount = ref(0)

        const btnSelected = (type) => {
            switch (type) {
                case 'useful':
                    usefulSelected.value = !usefulSelected.value
                    if (usefulCount.value === dbUsefulCount) {
                        usefulCount.value++
                    } else {
                        usefulCount.value = dbUsefulCount
                    }
                    break;
                case 'funny':
                    funnySelected.value = !funnySelected.value
                    if (funnyCount.value === dbFunnyCount) {
                        funnyCount.value++
                    } else {
                        funnyCount.value = dbFunnyCount
                    }
                    break;
                default:
                    coolSelected.value = !coolSelected.value
                    if (coolCount.value === dbcoolCount) {
                        coolCount.value++
                    } else {
                        coolCount.value = dbcoolCount
                    }
                    break;
            }
        }

        return {
            usefulSelected,
            funnySelected,
            coolSelected,
            usefulCount,
            funnyCount,
            coolCount,
            btnSelected
        }
    }
}
</script>

