<template>
  <b-row class="rest-container">
    <b-col sm="3">
        <div  class="rest-image"></div>
    </b-col>
    <b-col sm="9">
        <h4>{{ rest.name }}</h4>
        <div class="ratings">
            <div class="empty-stars"></div>
            <div class="full-stars" :style=" { width: starPercentage }"></div>
        </div>
        <span class="reviews-num">{{ rest.numberReviews }}</span>
        <div class="tag mt-2 mb-3">{{rest.category}}</div>
        <button class="btn-brand" @click="goRest(rest.id)">Read reviews</button>
    </b-col>
  </b-row>
</template>

<script>
import { computed } from '@vue/runtime-core'
import { useRouter } from 'vue-router'
import getStar from '../Composables/StarCalculator'

export default {
    props: ['rest'],
    setup(props) {
        const starPercentage = computed(() => {
            return getStar(props.rest.stars, props.rest.numberReviews)
        })

        const router = useRouter()

        const goRest = (id) => {
            router.push({ name: 'rest', params: { id: id }})
        }

        return {
            starPercentage,
            goRest
        }
    }
}
</script>

