  <script>
  import { ref } from 'vue'
  import { useRouter } from 'vue-router'

  export default {
      setup() {
          const currentUser = ref(null)
          // const currentUser = ref({name: 'Megan'})

          const handleLogout = async () => {
              
          }

          const signinModalShow = ref(false)
          const signupModalShow = ref(false)
          const ownerModalOpen = ref(false)


          const resetModal = (type) => {
            if (type === 'signin') {
                signinModalShow.value = false
            } else if (type === 'signup') {
                signupModalShow.value = false
            } else {
                ownerModalOpen.value = false
            }
          }

          const router = useRouter()
          const toOwnerSignup = () => {
            signupModalShow.value = false
            router.push({ name: 'ownerSignup'})
          }

          return { 
            handleLogout, 
            currentUser,
            signinModalShow,
            signupModalShow,
            resetModal,
            toOwnerSignup,
            ownerModalOpen
        }
      }
  }
